.landing {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landing--container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width:100%;
  position: relative;
}

.landing--img {
  opacity: 0;
  --img-size: 200px;
  position: absolute;
  left: 35%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: var(--img-size);
  height: var(--img-size);
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
  transition: opacity 0.3s;
}

.landing--img:hover {
  opacity: 1;
}

.landing--container-left {
  flex: 35% 1;
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.landing--social2 {
  display: none;
}

.lcl--content {
  /* margin: 3rem; */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* color:#eaeaea; */
}

.landing--social {
  font-size: 2rem;
  margin: 0 .5rem;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  padding-bottom: .2rem;
  /* color: #eaeaea; */
}

.landing--social:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: #fca311;
}

.landing--social2 {
  font-size: 35px;
  margin: 0 .3rem;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.landing--social2:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: #fca311;

}

.landing--container-right {
